import { module } from './module';

class Controller {
    key: string = "devices";
    clientData: any;
    itemToolbars: Array<any> = [];

    constructor(private $scope, private fxAction) {
        this.clientData = {
            rows: []
        };
        this.itemToolbars = [
            this.actionModel.list.toolbars.push(this.toolbarUtils.btnBuilder("刷新", "md-icon-button", false).iconBuilder("refresh", { fill: "black" }).btnClick(($event) => {
                this.doSearch(this.queryData.where || {});
            }).toValue())
        ];
    }
}

Controller.$inject = ["$scope", "fxAction"];

function Directive(): ng.IDirective {
    return {
        restrict: 'EA',
        template: require("./tpls/query.table.jade"),
        controller: Controller,
        controllerAs: 'qtCtl',
        link: function ($scope, $element, $attrs, $ctrl) {

        }
    };
}

module.directive('fxQueryTable', Directive);


